@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*, *::before, *::after{
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
}

header{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 22px;
    color: white;
    background-color: #2B76CC;
    font-family: Poppins, sans-serif;
}
h1{
    font-weight: bolder;
    font-size: 35px;
}
li{
    width: 100px;
    height: 40px;
    border-radius: 4px;
    list-style: none;
    text-align: center;
    background-color: inherit;
    font-size: 25px;
    cursor: pointer;
}

li:hover{
    background-color: #105BB0;
}
.loader{
    position: absolute;
    top: 45%;
    left: 45%;
    transform: translate(-50%,-50%);
    height: 100px;
    width: 100px;
    border : 10px solid rgb(223, 223, 223);
    border-radius: 50%;
    border-top: 10px solid #2B76CC;
    border-bottom: 10px solid #2B76CC;
    -webkit-animation: loading 4s infinite;
            animation: loading 4s infinite;
}

@-webkit-keyframes loading{
    0%{
        transform: rotate(0deg);
    }
    25%{
        transform: rotate(90deg);
    }
    50%{
        transform: rotate(180deg);
    }
    75%{
        transform: rotate(270deg);
    }
    100%{
        transform: rotate(360deg);
    }
}

@keyframes loading{
    0%{
        transform: rotate(0deg);
    }
    25%{
        transform: rotate(90deg);
    }
    50%{
        transform: rotate(180deg);
    }
    75%{
        transform: rotate(270deg);
    }
    100%{
        transform: rotate(360deg);
    }
}
*,*::before,*::after{
    padding: none;
    margin: none;
    box-sizing: border-box;
}

*::-webkit-scrollbar{
    display: none;
}

.container-quiz{
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    padding: 20px;
    font-family: Poppins,sans-serif;
}

.option{
    max-width: 500px;
    max-height: 200px;
    padding: 25px;
    margin: 25px;
    font-size: 22px;
    font-weight: bolder;
    color: white;
    background-color: #2B76CC;
    /* -webkit-text-stroke-width: 1px;
    -webkit-text-stroke-color: black; */
    font-family: inherit;
    border: none;
    cursor: pointer;
}

.option:hover{
    background-color: #105BB0 ;
}

h3{
    transform: translateX(20px);
    font-size: 25px;
}

.container-quiz-flag{
    display:flex;
}

img{
    max-width: 700px;
    max-height: 400px;
    margin: none;
    padding: none;
    position: fixed;
    border: 5px solid black;
    background-color: black;
    object-fit: cover;
    overflow: auto;
    top: 50%;
    left: 50%;
    transform: translate(0,-50%);
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

